.mv h2::after {
  content: "";
  z-index: 1;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  width: 2.5rem;
  height: 3px;
  background-color: #eb5a3c;
}
.mv__step-count {
  text-align: center;
  padding: 0;
