.mv__step-count > li {
  position: relative;
  list-style: none;
  display: inline-block;
  vertical-align: baseline;
  counter-increment: steps;
  width: 80px;
  text-align: center;
}
.mv__step-count > li::after {
  content: "";
  position: absolute;
  top: 12px;
  left: 50%;
  display: inline-block;
  width: 100%;
  height: 3px;
  margin: 0;
  background: #f7f7f7;
}
.mv__step-count > li > * {
  position: relative;
  display: block;
  padding-top: 2rem;
  font-size: 12px;
  font-style: normal;
}
.mv__step-count > li > *::before {
  content: counter(steps);
  position: absolute;
  z-index: 1;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 50%;
  background-color: #fff;
  border: 1px solid #eb5a3c;
  height: 26px;
  width: 26px;
  line-height: 26px;
  font-size: 12px;
  color: #eb5a3c;
}
.mv__step-count > li.current > *::before {
  content: counter(steps);
  background-color: #eb5a3c;
  color: #fff;
}
.mv__step-count > li.visited::after {
  background-color: #eb5a3c;
}
.mv__step-count > li.visited > *::before {
  content: counter(steps);
  background-color: #eb5a3c;
  color: #fff;
}
.mv__step-count > li:last-child::after {
  content: none;
}

